/* Header of the top */
#header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem 1rem;
  font-size: x-small;
  width: 100%;
  border-radius: 1rem;
  background-color: rgb(234, 236, 239);
}



/* Body of the weatherData */
section {
  width: 100%;
  height: 100vh;
  display: flex;
  /* align-items: center; */
  /* justify-content: center; */
  background: #f5f5f5;
  padding: 0.5rem;
  /* overflow: hidden; */
}

div#overlay {
  box-shadow: 0px 9px 30px rgba(5, 84, 255, 0.3);
  border-radius: 1rem;
  padding: 2rem;
  width: 300px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

div#overlay small {
  display: none;
}

@media screen and (max-width:1300px) {
  div#overlay div#startApp {
    display: none;
  }
  div#overlay small {
    display: block;
  }
}

