/* CSS */
.button {
  align-items: center;
  padding: 6px 14px;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', sans-serif;
  border-radius: 6px;
  color: #3D3D3D;
  background: #fff;
  border: none;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  /* box-shadow: 0px 0.5px 1px rgba(0, 0, 0, 0.1); */
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button:focus {
  box-shadow: 0px 0.5px 1px rgba(0, 0, 0, 0.1), 0px 0px 0px 3.5px rgba(58, 108, 217, 0.5);
  outline: 0;
}




h1, h6, p, h2 {
  margin: 0rem;
}

/* Main content of Weather app */
div.firstItem {
  flex-grow: 1;
  display: inherit;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem;
  /* background-color: red; */
  /* background-color: #fafafa; */

}

/* List styling css */
ul {
  display: flex;
  list-style: none;
  padding: 0rem;
  /* border: 1px solid green; */
}

li {
  padding: 0.25rem;
}
li > div {
  padding: 0.5rem;
  border-radius: 0.5rem;
  background-color: #f5f5f5;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
  /* text-align: center; */
  /* border: 1px solid red; */
}

/* current weather styling */
div#currentWeather {
  display: flex;
  width: 90%;
  justify-content: space-between;
  padding: 1rem 0rem;
}

div#currentWeather h1 {
  font-size: xx-large;
}

/* div#currentWeather div {
  border: 1px solid green;
} */

/* Weather details */
div#weatherDetails, div#hourlyWeather {
  background-color: rgb(234, 236, 239);
  padding: 1rem;
  border-radius: 2rem;
  width: 95%;
}
div#weatherDetails h2, div#weatherDetails h1 {
  font-size: medium;
}
div#weatherDetails h2 {
  padding-left: 1rem;
}
div#weatherDetails ul {
  flex-direction: row;
  overflow-x: auto;
  margin: 0.5rem 0rem;
}

div#weatherDetails li {
  flex-basis: 20%;
  flex-shrink: 0;
  text-align: center;
}

div#weatherDetails li p {
  margin-bottom: 0.75rem;
}


/* Start up design */

em {
  padding-right: 1rem;
}

/* Hourly weather details */

div#hourlyWeather {
  margin-top: 0.5rem;
}

div#hourlyWeather h1 {
  font-size: medium;
}

div#hourlyWeather ul {
  flex-direction: row;
  overflow-x: auto;
  margin: 0.5rem 0rem;
}

div#hourlyWeather li {
  flex-basis: 12.5%;
  flex-shrink: 0;
  text-align: center;
}

/* Second block CSS */

div#secondItem {
  background-color: rgb(234, 236, 239);
  border-radius: 2rem;
  /* width: 80%; */
  padding: 0.5rem 1rem;
}

div#secondItem h2 {
  font-size: small;
  text-align: center;
}

div#secondItem ul {
  display: block;
  margin: 0.5rem;
}
div#secondItem li {
  width: 12rem;
}

